import React, { useState } from 'react';

const ProductForm = () => {
    const [productData, setProductData] = useState({
        codigo: '',
        descripcion: '',
        unidCaja: 0,
        stockTotal: 0,
        consumos: Array(12).fill(0), // 12 meses de consumo
    });

    const handleChange = (e: React.ChangeEvent<HTMLInputElement>, index?: number) => {
        const { name, value } = e.target;
        if (index !== undefined) {
            const newConsumos = [...productData.consumos];
            newConsumos[index] = Number(value);
            setProductData({ ...productData, consumos: newConsumos });
        } else {
            setProductData({ ...productData, [name]: value });
        }
    };

    const handleSubmit = async (e: React.FormEvent) => {
        e.preventDefault();

        // Validar que haya al menos 5 consumos
        const validConsumptions = productData.consumos.filter((consumo) => consumo > 0).length;
        if (validConsumptions < 5) {
            alert('Debe ingresar al menos 5 consumos.');
            return;
        }

        try {
            const response = await fetch('/api/products', {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify(productData),
            });

            if (response.ok) {
                alert('Producto guardado correctamente');
            } else {
                alert('Error al guardar el producto');
            }
        } catch (error) {
            console.error(error);
            alert('Hubo un error al intentar guardar el producto');
        }
    };

    return (
        <form onSubmit={handleSubmit} className="max-w-lg mx-auto p-4 bg-white rounded-lg shadow-lg">
            <h2 className="text-2xl mb-4">Agregar Producto</h2>

            <div className="mb-4">
                <label htmlFor="codigo" className="block text-sm font-medium text-gray-700">
                    Código
                </label>
                <input
                    type="text"
                    id="codigo"
                    name="codigo"
                    value={productData.codigo}
                    onChange={handleChange}
                    className="mt-1 p-2 w-full border border-gray-300 rounded"
                    required
                />
            </div>

            <div className="mb-4">
                <label htmlFor="descripcion" className="block text-sm font-medium text-gray-700">
                    Descripción
                </label>
                <input
                    type="text"
                    id="descripcion"
                    name="descripcion"
                    value={productData.descripcion}
                    onChange={handleChange}
                    className="mt-1 p-2 w-full border border-gray-300 rounded"
                />
            </div>

            <div className="mb-4">
                <label htmlFor="unidCaja" className="block text-sm font-medium text-gray-700">
                    Unidad/Caja
                </label>
                <input
                    type="number"
                    id="unidCaja"
                    name="unidCaja"
                    value={productData.unidCaja}
                    onChange={handleChange}
                    className="mt-1 p-2 w-full border border-gray-300 rounded"
                />
            </div>

            <div className="mb-4">
                <label htmlFor="stockTotal" className="block text-sm font-medium text-gray-700">
                    Stock Total
                </label>
                <input
                    type="number"
                    id="stockTotal"
                    name="stockTotal"
                    value={productData.stockTotal}
                    onChange={handleChange}
                    className="mt-1 p-2 w-full border border-gray-300 rounded"
                />
            </div>

            <div className="grid grid-cols-3 gap-4">
                {productData.consumos.map((consumo, index) => (
                    <div key={index} className="mb-4">
                        <label className="block text-sm font-medium text-gray-700">
                            Consumo {['ENE', 'FEB', 'MAR', 'ABR', 'MAY', 'JUN', 'JUL', 'AGO', 'SEP', 'OCT', 'NOV', 'DIC'][index]} 2024
                        </label>
                        <input
                            type="number"
                            value={consumo}
                            onChange={(e) => handleChange(e, index)}
                            className="mt-1 p-2 w-full border border-gray-300 rounded"
                        />
                    </div>
                ))}
            </div>

            <button type="submit" className="mt-4 p-2 w-full bg-blue-600 text-white rounded">
                Guardar Producto
            </button>
        </form>
    );
};

export default ProductForm;
